import videoobject from "@/components/script/videoobject";
import videoitemobject from "@/components/script/videoitemobject";


export default {
  name: "video_item",
  mixins: [videoobject, videoitemobject],
  props: {
    topic_data: {},
    show_node: undefined,
    show_image: undefined,
    set_top: undefined
  }
  ,
  data: function () {
    return {
      preivew_url: undefined,
      online_video: undefined
    }
  }
  ,
  created() {
    try {
      if (this.topic_data && this.topic_data.video_info) {
        this.online_video = this.topic_data.video_info.online_videos[0];
      }
      console.log('[video_item]this.topic_data:', this.topic_data);
    } catch (e) {

    }
  }
  ,
  computed: {
    resolution_description_mark: function () {
      var resolution_description = this.resolution_description();
      return resolution_description;
    },
    is_valid: function () {
      if (!this.topic_data || !this.online_video) {
        return false;
      }
      if (!this.topic_data.video_info) {
        return false;
      }
      return true;
    }
  }
  ,
  methods: {}
}
