import links from '@/components/common/links'

export default {
  name: 'appfooter',
  components: {
    links
  },
  data() {
    return {}
  },
  computed: {
    app_url: function () {
      var app_conf = this.$ads_object.text['pf.app'];
      if (app_conf) {
        return app_conf['url'];
      }
      return undefined;
    }
  }
}
